// === mixins ===
@mixin button-main {
	padding: 5px 15px;
	border: none;
	border-radius: 5px;
	outline: none;
	background-color: blue;
	color: #fff;
	font-size: 16px;
	font-weight: bold;
	cursor: pointer;

	&:hover {
		text-decoration: underline;
	}
}
// === / mixins ===

// === variables ===
$color-white: #fff;
$color-main: #009dff;
$color-text: #333;
// === / variables ===
